import React from 'react';
import Customers from '../Customers.json';

function Home() {
    let CustomerData = Customers.map((el, i) => {
        return(
            <tr key={i}>
                <td>{el.Name}</td>
                <td>{el.City}</td>
                <td>{el.Country}</td>
            </tr>
        );
    });

return (
    <>
        <h2>Home</h2>
        <table className="table table-bordered">
            <thead>
                <tr>
                    <th>Name</th>
                    <th>City</th>
                    <th>Country</th>
                </tr>
            </thead>
            <tbody>
                {CustomerData}
            </tbody>
        </table>  
    </> 
);
}

export default Home;